___CSS_LOADER_EXPORT___.push([module.id, "th.active{color:#fff}th.active .arrow{opacity:1}.arrow{display:inline-block;height:0;margin-left:5px;opacity:.66;vertical-align:middle;width:0}.arrow.asc{border-bottom:4px solid #fff}.arrow.asc,.arrow.dsc{border-left:4px solid transparent;border-right:4px solid transparent}.arrow.dsc{border-top:4px solid #fff}", "",{"version":3,"sources":["webpack://./resources/js/components/Grid2.vue"],"names":[],"mappings":"AAkFQ,UACE,UACF,CAEA,iBACE,SACF,CAEA,OACE,oBAAqB,CAGrB,QAAS,CACT,eAAgB,CAChB,WAAa,CAJb,qBAAsB,CACtB,OAIF,CAEA,WAGE,4BACF,CAEA,sBALE,iCAAkC,CAClC,kCAQF,CAJA,WAGE,yBACF","sourcesContent":["<script>\r\n    export default {\r\n      props: {\r\n        data: Array,\r\n        columns: Array,\r\n        filterKey: String\r\n      },\r\n      data() {\r\n        return {\r\n          sortKey: '',\r\n          sortOrders: this.columns.reduce((o, key) => ((o[key] = 1), o), {})\r\n        }\r\n      },\r\n      computed: {\r\n        filteredData() {\r\n          const sortKey = this.sortKey\r\n          const filterKey = this.filterKey && this.filterKey.toLowerCase()\r\n          const order = this.sortOrders[sortKey] || 1\r\n          let data = this.data\r\n          if (filterKey) {\r\n            data = data.filter((row) => {\r\n              return Object.keys(row).some((key) => {\r\n                return String(row[key]).toLowerCase().indexOf(filterKey) > -1\r\n              })\r\n            })\r\n          }\r\n          if (sortKey) {\r\n            data = data.slice().sort((a, b) => {\r\n              a = a[sortKey]\r\n              b = b[sortKey]\r\n              return (a === b ? 0 : a > b ? 1 : -1) * order\r\n            })\r\n          }\r\n          return data\r\n        }\r\n      },\r\n      methods: {\r\n        sortBy(key) {\r\n          this.sortKey = key\r\n          this.sortOrders[key] = this.sortOrders[key] * -1\r\n        },\r\n        capitalize(str) {\r\n          return str.charAt(0).toUpperCase() + str.slice(1)\r\n        }\r\n      }\r\n    }\r\n    </script>\r\n    \r\n    <template>\r\n\r\n            <div class=\"flex-grow overflow-auto\">\r\n           \r\n      <table class=\"table-auto relative w-full border border-separate text-break text-sm\" v-if=\"filteredData.length\">\r\n        <thead class=\"bg-blue-500 text-white\">\r\n          <tr>\r\n            <th class=\"p-3\" v-for=\"key in columns\"\r\n              @click=\"sortBy(key)\"\r\n              :class=\"{ active: sortKey == key }\">\r\n              {{ capitalize(key) }}\r\n              <span class=\"arrow\" :class=\"sortOrders[key] > 0 ? 'asc' : 'dsc'\">\r\n              </span>\r\n            </th>\r\n          </tr>\r\n        </thead>\r\n        <tbody>\r\n          <tr  class=\"bg-blue-200 lg:text-black\" v-for=\"entry in filteredData\">\r\n            <td class=\"p-3 font-medium capitalize\" v-for=\"key in columns\">\r\n            <h5>{{entry[key]}}</h5>  \r\n            </td>\r\n            \r\n          </tr>\r\n        </tbody>\r\n      </table>\r\n      <p v-else>No matches found.</p>\r\n    </div>\r\n\r\n \r\n    </template>\r\n    \r\n    <style>\r\n        \r\n        \r\n        th.active {\r\n          color: #fff;\r\n        }\r\n        \r\n        th.active .arrow {\r\n          opacity: 1;\r\n        }\r\n        \r\n        .arrow {\r\n          display: inline-block;\r\n          vertical-align: middle;\r\n          width: 0;\r\n          height: 0;\r\n          margin-left: 5px;\r\n          opacity: 0.66;\r\n        }\r\n        \r\n        .arrow.asc {\r\n          border-left: 4px solid transparent;\r\n          border-right: 4px solid transparent;\r\n          border-bottom: 4px solid #fff;\r\n        }\r\n        \r\n        .arrow.dsc {\r\n          border-left: 4px solid transparent;\r\n          border-right: 4px solid transparent;\r\n          border-top: 4px solid #fff;\r\n        }\r\n        </style>"],"sourceRoot":""}]);
