is.users) ;\r\n                    console.log(this.userss);\r\n                   \r\n                    // this.size=this.$refs.infoBox.clientWidth;\r\n                    // this.Height= size + 'px' ;\r\n                    // console.log(this.size);\r\n                      });\r\n              },\r\n              tattruyen(){\r\n                this.userss = '' ;\r\n                this.hientruyen = 'ok' ;\r\n                console.log(this.userss);\r\n              }\r\n          }\r\n          ,\r\n      mounted() {\r\n        // var size = window.innerHeight / 30 ;\r\n        var size = this.$refs.infoBox.clientHeight / 20;\r\n       \r\n        this.Height= size + 'px' ;\r\n    \r\n        console.log(this.Height);\r\n    \r\n      }\r\n        }\r\n        </script>\r\n   <style>\r\n    .chu {\r\n        font-size: 12px; \r\n        height: auto;\r\n       /* font-weight: inherit; */\r\n   }\r\n   .test {\r\n\r\n   }\r\n   </style>"],"sourceRoot":""}]);
