___CSS_LOADER_EXPORT___.push([module.id, ".elapsed-container{width:300px}.elapsed-bar{background-color:red;height:10px}", "",{"version":3,"sources":["webpack://./resources/js/components/time.vue"],"names":[],"mappings":"AA4CI,mBACE,WACF,CAEA,aACE,oBAAqB,CACrB,WACF","sourcesContent":["<!--\r\nhttps://eugenkiss.github.io/7guis/tasks/#timer\r\n-->\r\n\r\n<script>\r\n    export default {\r\n      data() {\r\n        return {\r\n          duration: 15 * 1000,\r\n          elapsed: 0\r\n        }\r\n      },\r\n      created() {\r\n        let lastTime = performance.now()\r\n        const update = () => {\r\n          const time = performance.now()\r\n          this.elapsed += Math.min(time - lastTime, this.duration - this.elapsed)\r\n          lastTime = time\r\n          this.handle = requestAnimationFrame(update)\r\n        }\r\n        update()\r\n      },\r\n      unmounted() {\r\n        cancelAnimationFrame(this.handle)\r\n      }\r\n    }\r\n    </script>\r\n    \r\n    <template>\r\n      <label\r\n        >Elapsed Time: <progress :value=\"elapsed / duration\"></progress\r\n      ></label>\r\n    \r\n      <div>{{ (elapsed / 1000).toFixed(1) }}s</div>\r\n    \r\n      <div>\r\n        Duration: <input type=\"range\" v-model=\"duration\" min=\"1\" max=\"30000\">\r\n        {{ (duration / 1000).toFixed(1) }}s\r\n      </div>\r\n    \r\n      <button @click=\"elapsed = 0\">Reset</button>\r\n    </template>\r\n    \r\n    <style>\r\n    .elapsed-container {\r\n      width: 300px;\r\n    }\r\n    \r\n    .elapsed-bar {\r\n      background-color: red;\r\n      height: 10px;\r\n    }\r\n    </style>"],"sourceRoot":""}]);
